import {AfterViewInit, Component, OnDestroy, OnInit} from '@angular/core';
import {InterfaceService} from '../interface.service';
import {Interace, INTERFACE, RESPONSETIME, ResponseTime} from '../interace';


@Component({
  selector: 'ngx-brand',
  templateUrl: './response.component.html',
  styleUrls: ['./response.component.scss'],
})
export class ResponseComponent implements OnInit {
  options: any = {};
  mInstance: any;
  responseData: ResponseTime = RESPONSETIME;

  chartInit(ev): void {
    this.mInstance = ev;
  }

  constructor(private interfaceService: InterfaceService) {
    interfaceService.changeTitle('响应时间');
    interfaceService.getResponseData(this.interfaceService.getRequestInterface()).then(z => {
      this.responseData = z as ResponseTime;
      console.log('获取数据');
      this.responseData.date = this.responseData.date.map(function (str) {
        return str.replace(' ', '\n');
      });
      console.log(this.responseData);
      this.options.xAxis[0].data = this.responseData.date;
      this.options.series[0].data = this.responseData.conut;
      this.options.series[0].markLine.data[0].yAxis = this.responseData.allAvg;
      this.mInstance.setOption(this.options);
    });
    interfaceService.getInterfaceSubject().subscribe(x => {
      interfaceService.getResponseData(x).then(z => {
        this.responseData = z as ResponseTime;
        console.log('获取到数据');
        console.log(this.responseData);
        this.responseData.date = this.responseData.date.map(function (str) {
          return str.replace(' ', '\n');
        });
        this.options.xAxis[0].data = this.responseData.date;
        this.options.series[0].data = this.responseData.conut;
        this.options.series[0].markLine.data[0].yAxis = this.responseData.allAvg;
        this.mInstance.setOption(this.options);
      });
    });
  }

  ngOnInit() {
    this.responseData.date = this.responseData.date.map(function (str) {
      return str.replace(' ', '\n');
    });
    this.options = {
      tooltip: {
        trigger: 'axis',
        axisPointer: {
          animation: false,
        },
      },
      toolbox: {
        feature: {
          dataZoom: {
            yAxisIndex: 'none',
          },
          restore: {},
          saveAsImage: {},
        },
      },
      axisPointer: {
        link: {xAxisIndex: 'all'},
      },
      grid: [{
        left: 50,
        right: 50,
        height: '70%',
      }],
      xAxis: [
        {
          type: 'category',
          boundaryGap: false,
          axisLine: {onZero: true},
          data: this.responseData.date,
        },
      ],
      yAxis: [
        {
          name: '时长(ms)',
          type: 'value',
        },
      ],
      dataZoom: [{
        textStyle: {
          color: '#8392A5',
        },
        handleSize: '80%',
        dataBackground: {
          areaStyle: {
            color: '#8392A5',
          },
          lineStyle: {
            opacity: 0.8,
            color: '#8392A5',
          },
        },
        handleStyle: {
          color: '#fff',
          shadowBlur: 3,
          shadowColor: 'rgba(0, 0, 0, 0.6)',
          shadowOffsetX: 2,
          shadowOffsetY: 2,
        },
      }, {
        type: 'inside',
      }],
      series: [
        {
          name: '时长',
          type: 'line',
          symbolSize: 8,
          hoverAnimation: false,
          markLine: {
            data: [
              {
                name: '平均值',
                yAxis: this.responseData.allAvg,
              },
            ],
          },
          data: this.responseData.conut,
        },
        {
          name: '访问量',
          type: 'line',
          symbolSize: 8,
          hoverAnimation: false,
          data: this.responseData.avgTime,
          itemStyle: {
            // 设置扇形的颜色
            color: 'transparent',
          },
        },
      ],
    };

  }
}



