import {NgModule} from '@angular/core';
import {NgxEchartsModule} from 'ngx-echarts';


import {ThemeModule} from '../../@theme/theme.module';
import {TabsModule} from 'ngx-bootstrap';
import {SmartTableService} from '../../@core/data/smart-table.service';
import {Ng2SmartTableModule} from 'ng2-smart-table';
import {InterfaceComponent} from './interface.component';
import {InterfaceRoutingModule} from './interface-routing.module';
import {PvComponent} from './pv/pv.component';
import {UvComponent} from './uv/uv.component';
import {ResponseComponent} from './response/response.component';
import {InterfaceService} from './interface.service';


const homes = [
];

const components = [
  InterfaceComponent,
  PvComponent,
  UvComponent,
  ResponseComponent,
];

@NgModule({
  imports: [ThemeModule, InterfaceRoutingModule, NgxEchartsModule, Ng2SmartTableModule, TabsModule.forRoot()],
  declarations: [...components, ...homes],
  providers: [
    SmartTableService,
    InterfaceService,
  ],
})
export class InterfaceModule {
}



