import {AfterViewInit, Component, OnDestroy, OnInit} from '@angular/core';
import {InterfaceService} from '../interface.service';
import {Interace, INTERFACE} from '../interace';


@Component({
  selector: 'ngx-brand',
  templateUrl: './pv.component.html',
  styleUrls: ['./pv.component.scss'],
})
export class PvComponent implements OnInit {
  options: any = {};
  mInstance: any;
  pvData: Interace = INTERFACE;

  chartInit(ev): void {
    this.mInstance = ev;
    /*this.mInstance.on('click', param => {
      console.log(param);
    });*/
  }

  constructor(private interfaceService: InterfaceService) {
    interfaceService.changeTitle('PV');
    interfaceService.getPvData(this.interfaceService.getRequestInterface()).then(z => {
      this.pvData = z as Interace;
      console.log('获取数据');
      console.log(this.pvData);
      this.pvData.date = this.pvData.date.map(function (str) {
        return str.replace(' ', '\n');
      });
      this.options.xAxis[0].data = this.pvData.date;
      this.options.series[0].data = this.pvData.count;
      this.mInstance.setOption(this.options);
    });
    interfaceService.getInterfaceSubject().subscribe(x => {
      interfaceService.getPvData(x).then(z => {
        this.pvData = z as Interace;
        console.log('获取到数据');
        console.log(this.pvData);
        this.pvData.date = this.pvData.date.map(function (str) {
          return str.replace(' ', '\n');
        });
        this.options.xAxis[0].data = this.pvData.date;
        this.options.series[0].data = this.pvData.count;
        this.mInstance.setOption(this.options);
      });
    });
  }

  ngOnInit() {
    this.pvData.date = this.pvData.date.map(function (str) {
      return str.replace(' ', '\n');
    });
    this.options = {
      tooltip: {
        trigger: 'axis',
        axisPointer: {
          animation: false,
        },
      },
      toolbox: {
        feature: {
          dataZoom: {
            yAxisIndex: 'none',
          },
          restore: {},
          saveAsImage: {},
        },
      },
      axisPointer: {
        link: {xAxisIndex: 'all'},
      },
      grid: [{
        left: 50,
        right: 50,
        height: '70%',
      }],
      xAxis: [
        {
          type: 'category',
          boundaryGap: false,
          axisLine: {onZero: true},
          data: this.pvData.date,
        },
      ],
      yAxis: [
        {
          name: '访问量(人)',
          type: 'value',
        },
      ],
      dataZoom: [{
        textStyle: {
          color: '#8392A5',
        },
        handleSize: '80%',
        dataBackground: {
          areaStyle: {
            color: '#8392A5',
          },
          lineStyle: {
            opacity: 0.8,
            color: '#8392A5',
          },
        },
        handleStyle: {
          color: '#fff',
          shadowBlur: 3,
          shadowColor: 'rgba(0, 0, 0, 0.6)',
          shadowOffsetX: 2,
          shadowOffsetY: 2,
        },
      }, {
        type: 'inside',
      }],
      series: [
        {
          name: '访问量',
          type: 'line',
          symbolSize: 8,
          hoverAnimation: false,
          data: this.pvData.count,
        },
      ],
    };
  }
}



