import {HttpClient, HttpHeaders, HttpParams} from '@angular/common/http';
import {Injectable} from '@angular/core';
import {Observable} from 'rxjs/Observable';
import {Subject} from 'rxjs/Subject';
import {RequestInterface} from './interace';
import * as moment from 'moment';

@Injectable()
export class InterfaceService {
  private BaseUrl = 'http://10.0.0.30:8000/';
  private headers = new HttpHeaders().set('Content-Type', 'application/json');

  private titleSubject = new Subject<string>();
  private requestSubject = new Subject<RequestInterface>();
  private requestInterface: RequestInterface;
  private kind: string = '昨日';

  constructor(private http: HttpClient) {
    this.requestInterface = new RequestInterface();
    this.requestInterface.apiIndex = 4;
    this.requestInterface.type = 1;
    this.requestInterface.endTime = moment().subtract(1, 'days').format('YYYY-MM-DD');
  }

  getRequestInterface(): RequestInterface {
    return this.requestInterface;
  }

  getTitleSubject(): Subject<string> {
    return this.titleSubject;
  }

  changeTitle(title: string): void {
    this.titleSubject.next(title);
  }

  changeTime(requestInterface: RequestInterface): void {
    this.requestInterface.endTime = requestInterface.endTime;
    this.requestInterface.type = requestInterface.type;
    this.requestSubject.next(this.requestInterface);
  }

  getInterfaceSubject(): Subject<RequestInterface> {
    return this.requestSubject;
  }

  getPvData(requestInterface: RequestInterface) {
    const url = this.BaseUrl + 'getPVByTimeStamp';
    const params = new HttpParams().set('end', this.requestInterface.endTime).append('apiIndex', this.requestInterface.apiIndex.toString()).append('type', this.requestInterface.type.toString());
    const options = {params: params};
    return this.http.get(url, options).toPromise();
  }

  getUvData(requestInterface: RequestInterface) {
    const url = this.BaseUrl + 'getUVByDateAndApiAndType';
    const params = new HttpParams().set('end', this.requestInterface.endTime).append('apiIndex', this.requestInterface.apiIndex.toString()).append('type', this.requestInterface.type.toString());
    const options = {params: params};
    return this.http.get(url, options).toPromise();
  }

  getResponseData(requestInterface: RequestInterface) {
    const url = this.BaseUrl + 'mongo/responseTime/getHourRTByDateAndApiId';
    const params = new HttpParams().set('end', this.requestInterface.endTime).append('index', this.requestInterface.apiIndex.toString()).append('type', this.requestInterface.type.toString());
    const options = {params: params};
    return this.http.get(url, options).toPromise();
  }
}



